import { Component, Inject, OnInit } from '@angular/core';
import { IArticulo } from '../../models/articulo';
import { ArticulosService } from '../../service/articulos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { IRubro } from 'app/modules/stock/rubros/models/rubro';
import { firstValueFrom } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { DatePipe } from '@angular/common';
import { MAT_DATE_FORMATS } from '@angular/material/core';
import { MY_DATE_FORMATS } from '@shared/constants/date-format';
import { AuthService } from '@core/auth/auth.service';

@Component({
    selector: 'app-save-articulos',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class SaveComponent implements OnInit {
    columns = ['codigo_interno', 'descripcion', 'rubro', 'tipo', 'activo'];
    form: FormGroup;
    selectedOption: string = '';
    TipoArticulo: string[] = [];
    articulo: IArticulo;
    title: string;
    rubros: IRubro[];
    minDate: Date;
    maxDate: Date;

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private authService: AuthService,
        private articulosService: ArticulosService,
        private datePipe: DatePipe,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.articulo = this.data ? this.data : null;
        this.title = this.articulo?.idarticulo ? 'Editar articulo' : 'Nuevo articulo';
        // Se define minDate como null o cualquier otra fecha límite que desees permitir hacia atras
        this.minDate = null;
        // Define maxDate como el dia de hoy
        this.maxDate = new Date();
    }

    ngOnInit(): void {
        setTimeout(() => {
            this.getRubros();
            this.authService.verificarDatosEmpresaCargados().subscribe({
                next: (loaded) => {
                    if (loaded) {
                        this.getTipoArticulo();
                    }
                }
            });
        })
    }

    async getTipoArticulo() {
        this.TipoArticulo = this.authService.tiposArticuloPermitidos;
        // Verificar si solo hay un tipo de artículo permitido
        if (this.TipoArticulo.length === 1) {
            // Obtener el tipo de artículo permitido
            const tipo = this.TipoArticulo[0];
            // Seleccionar automáticamente ese tipo de artículo y cargar el formulario correspondiente
            if (tipo === 'insumo') {
                this.toggleForm('insumo', 0);
            } else if (tipo === 'producto') {
                this.toggleForm('producto', 1);
            } else if (tipo === 'servicio') {
                this.toggleForm('servicio', 2);
            }
        } else if (this.articulo && this.articulo.tipo !== undefined) { // Verificar si estás editando un artículo
            // Si estás editando un artículo, selecciona automáticamente el checkbox correspondiente
            if (this.articulo.tipo === 0) {
                this.toggleForm('insumo', 0);
            } else if (this.articulo.tipo === 1) {
                this.toggleForm('producto', 1);
            } else if (this.articulo.tipo === 2) {
                this.toggleForm('servicio', 2);
            }
        }
    }

    toggleForm(option: string, tipo: number) {
        this.selectedOption = option;
        if (tipo === 0) {
            this.form = new FormGroup({ 
                codigo_interno: new FormControl((this.articulo ? this.articulo.codigo_interno : ''), Validators.required),
                descripcion: new FormControl((this.articulo ? this.articulo.descripcion : ''), Validators.required),
                rubro: new FormControl((this.articulo ? this.articulo.idrubro : ''), Validators.required),
                stock_minimo: new FormControl((this.articulo ? this.articulo.stock_minimo : ''), [Validators.required, Validators.min(0.0)]),
                activo: new FormControl((this.articulo ? (this.articulo?.activo ? 1 : 0) : ''), Validators.required),
            })
        } else if (tipo === 1) {
            this.form = new FormGroup({ 
                codigo_interno: new FormControl((this.articulo ? this.articulo.codigo_interno : ''), Validators.required),
                descripcion: new FormControl((this.articulo ? this.articulo.descripcion : ''), Validators.required),
                rubro: new FormControl((this.articulo ? this.articulo.idrubro : ''), Validators.required),
                stock_minimo: new FormControl((this.articulo ? this.articulo.stock_minimo : ''), [Validators.required, Validators.min(0.0)]),
                activo: new FormControl((this.articulo ? (this.articulo?.activo ? 1 : 0) : ''), Validators.required),
                moneda: new FormControl((this.articulo ? (this.articulo?.moneda ? 1 : 0) : ''), Validators.required),
                precio_costo: new FormControl((this.articulo ? this.articulo.costo : ''),Validators.required),
                iva_proveedor: new FormControl((this.articulo ? this.articulo.iva_proveedor : ''), Validators.required),
                flete_porcentaje: new FormControl((this.articulo ? this.articulo.flete_porcentaje : ''), Validators.required),
                flete_importe: new FormControl((this.articulo ? this.articulo.flete_importe : ''), Validators.required),
                utilidad: new FormControl((this.articulo ? this.articulo.utilidad : ''), Validators.required),
                neto_sugerido: new FormControl((this.articulo ? this.articulo.neto_sugerido : ''), Validators.required),
                neto_final: new FormControl((this.articulo ? this.articulo.neto_final : ''), Validators.required),
                iva_porcentaje: new FormControl((this.articulo ? this.articulo.iva_porcentaje : ''), Validators.required),
                bruto_sugerido: new FormControl((this.articulo ? this.articulo.bruto_sugerido : ''), Validators.required),
                fecha_actualizacion: new FormControl((this.articulo?.fecha_actualizacion ? this.datePipe.transform(this.articulo.fecha_actualizacion, 'yyyy-MM-dd') : this.datePipe.transform(new Date(), 'yyyy-MM-dd')), [Validators.required]),
            })
        } else if (tipo === 2) {
            this.form = new FormGroup({ 
                codigo_interno: new FormControl((this.articulo ? this.articulo.codigo_interno : ''), Validators.required),
                descripcion: new FormControl((this.articulo ? this.articulo.descripcion : ''), Validators.required),
                rubro: new FormControl((this.articulo ? this.articulo.idrubro : ''), Validators.required),
                activo: new FormControl((this.articulo ? (this.articulo?.activo ? 1 : 0) : ''), Validators.required),
            })
        }
    }

    
    /**
     * Esta funcion la utilizamos cuando queremos listar elementos en un selector.
     */
    async getRubros() {
        const rubros = await firstValueFrom(this.articulosService.getRubros())
        this.getDataRubros(rubros);
    }

    async getDataRubros(data) {
        new Promise(async (resolve) => {
            this.rubros = data.list
            resolve(true)
        })
    }

     /**
     * Esta funcion calcula el importe del flete
     */
    calcularImporteFlete() {
        const precioCosto = this.form.get('precio_costo').value;
        const ivaProveedor = this.form.get('iva_proveedor').value;
        const porcentajeFlete = this.form.get('flete_porcentaje').value;
        const importeFlete = this.form.get('flete_importe').value;

        if (porcentajeFlete !== null && porcentajeFlete !== '') {
            // Calcular el importe del flete utilizando el porcentaje
            const flete = precioCosto * (1 + ivaProveedor / 100) * (porcentajeFlete / 100);
            this.form.patchValue({ 'flete_importe': flete });
        } else if (importeFlete !== null && importeFlete !== '') {
            // Si se ingresó un importe de flete, usar ese valor
            this.form.patchValue({ 'flete_porcentaje': null });
        }
    }

    /**
     * Esta funcion calcula el Neto Sugerido dependiendo del Precion de Costo, Iva Proveedor, Importe Flete y Utilidad
    */
    calcularNetoSugerido() {
        const precioCosto = this.form.get('precio_costo').value;
        const ivaProveedor = this.form.get('iva_proveedor').value;
        const importeFlete = this.form.get('flete_importe').value;
        const utilidad = this.form.get('utilidad').value;

        const neto_sugerido = ((precioCosto * (1 + ivaProveedor / 100) + importeFlete) * (1 + utilidad / 100)).toFixed(2);

        this.form.patchValue({ 'neto_sugerido': neto_sugerido });
    }

    /**
     * Esta funcion calcula el Precio de Venta Sugerido que se lo puede vender dependiendo del Precio Neto Final y Iva
    */
    calcularPrecioVentaSugerido() {
        const netoFinal = this.form.get('neto_final').value;
        const ivaPorcentaje = this.form.get('iva_porcentaje').value;

        const bruto_sugerido = (netoFinal * (1 + ivaPorcentaje / 100)).toFixed(2);
        
        this.form.patchValue({ 'bruto_sugerido': bruto_sugerido });
    }

    /**
     * Esta funcion es para manejar cambios en los campos relevantes
    */
    onInputChange() {
        this.calcularImporteFlete();
        this.calcularNetoSugerido();
        this.calcularPrecioVentaSugerido();
    } 

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            if (this.selectedOption === 'insumo'){
                this.articulo = {
                    ...this.articulo,
                    codigo_interno: this.form.get('codigo_interno').value.toUpperCase(),
                    descripcion: this.form.get('descripcion').value,
                    idrubro: this.form.get('rubro').value,
                    stock_minimo: this.form.get('stock_minimo').value,
                    activo: this.form.get('activo').value,
                    tipo: 0
                }
            } else if (this.selectedOption === 'producto'){
                const fecha = this.form.get('fecha_actualizacion').value;

                // Se formatea la fecha nuevamente al formato deseado (YYYY-MM-DD hh:mm:ss)
                const fecha_actualizacion = this.datePipe.transform(`${fecha}`, 'yyyy-MM-dd');

                this.articulo = {
                    ...this.articulo,
                    codigo_interno: this.form.get('codigo_interno').value.toUpperCase(),
                    descripcion: this.form.get('descripcion').value,
                    idrubro: this.form.get('rubro').value,
                    stock_minimo: this.form.get('stock_minimo').value,
                    activo: this.form.get('activo').value,
                    tipo: 1,
                    moneda: this.form.get('moneda').value,
                    costo: this.form.get('precio_costo').value,
                    iva_proveedor: this.form.get('iva_proveedor').value,
                    flete_porcentaje: this.form.get('flete_porcentaje').value,
                    flete_importe: this.form.get('flete_importe').value,
                    utilidad: this.form.get('utilidad').value,
                    neto_sugerido: this.form.get('neto_sugerido').value,
                    neto_final: this.form.get('neto_final').value,
                    iva_porcentaje: this.form.get('iva_porcentaje').value,
                    bruto_sugerido: this.form.get('bruto_sugerido').value,
                    fecha_actualizacion: fecha_actualizacion,
                }
            } else if (this.selectedOption === 'servicio'){
                this.articulo = {
                    ...this.articulo,
                    codigo_interno: this.form.get('codigo_interno').value.toUpperCase(),
                    descripcion: this.form.get('descripcion').value,
                    idrubro: this.form.get('rubro').value,
                    activo: this.form.get('activo').value,
                    tipo: 2
                }
            }
            if (this.articulo.idarticulo) {
                this.articulosService.updateArticulo(this.articulo)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            } else {
                this.articulosService.saveArticulo(this.articulo)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}